import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import bridge from '@vkontakte/vk-bridge';
import {Button, PanelHeader, PanelHeaderBack} from "@vkontakte/vkui";

export class Placeholder extends PureComponent {
    render() {
        const
            {t, icon, title, description, buttonText, buttonIcon, buttonOnClick, buttonMode, buttonBack} = this.props
        ;

        return (
            <React.Fragment>
                {buttonBack && <PanelHeader className='PanelHeader__Placeholder' separator={false} left={<PanelHeaderBack onClick={() => t.back()}/>}/>}
                <div className='Placeholder'>
                    {React.cloneElement(icon, {className: 'Placeholder__Icon'})}
                    <div>
                        <h1>{title}</h1>
                        <p>{description}</p>
                        {
                            buttonText &&
                            <Button
                                after={buttonIcon}
                                size='m' mode={buttonMode || 'gradient'}
                                onClick={buttonOnClick}
                            >{buttonText}</Button>
                        }
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

Placeholder.defaultProps = {};

Placeholder.propTypes = {
    icon: PropTypes.object.isRequired,
    title: PropTypes.string.isRequired,
    description: PropTypes.string,
    buttonText: PropTypes.string,
    buttonIcon: PropTypes.object,
    buttonOnClick: PropTypes.func,
    buttonMode: PropTypes.string,
    buttonBack: PropTypes.bool
};

export default Placeholder;