import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import {Avatar, PanelHeader, PanelHeaderBack, ScreenSpinner, Snackbar} from "@vkontakte/vkui";
import {decOfNum, getBase64Image, getSrcUrl, getUrlParams, isPlatformDesktop} from "../../js/utils";
import bridge from "@vkontakte/vk-bridge";
import {getVKUsers} from "../../js/drawerapp/utils";

export class Rating extends PureComponent {
    constructor(props) {
        super(props);

        this.state = {
            offset: props.t.state.gamesAsWinner.length,
            limit: 10,
            canGetMore: true,
            prevY: 0
        };
    }

    async componentDidMount() {
        this.observer = new IntersectionObserver(
            this.handleObserver.bind(this),
            {
                root: null,
                rootMargin: '0px',
                threshold: 1.0
            }
        );
        this.observer.observe(this.loadingRef);
    }

    handleObserver(entities, observer) {
        const
            {y} = entities[0].boundingClientRect,
            {t} = this.props,
            {prevY, offset, limit, canGetMore} = this.state
        ;
        if ((prevY === 0 || prevY > y) && canGetMore) {
            t.socket.call('games.getByWinnerId', {id: t.state.vk_user.id, offset, limit}, async r => {
                await getVKUsers(r.response.map(value => value.drawerId));
                t.setState({gamesAsWinner: [...t.state.gamesAsWinner, ...r.response]});
                this.setState({canGetMore: r.response.length > 0});
                this.forceUpdate();
            });
            this.setState({offset: offset + 10});
        }
        this.setState({prevY: y});
    }

    shareStory(gameId, pic) {
        this.props.t.setPopout(<ScreenSpinner/>);
        try {
			const
				{createCanvas, loadImage} = require('canvas')
			;

			loadImage(getSrcUrl(require(`../../assets/drawing/stories/Story2.png`))).then(async background => {
				const
					canvas = createCanvas(background.width, background.height),
					ctx = canvas.getContext('2d'),
					canvasData = await getBase64Image(pic)
				;
				ctx.drawImage(background, 0, 0);
				ctx.drawImage(await loadImage(canvasData), 198, 618, 683, 683);

				try {
					await bridge.send('VKWebAppShowStoryBox', {
						background_type: 'image',
						blob: canvas.toDataURL('image/png'),
						attachment: {
							url: 'https://vk.com/app' + getUrlParams().vk_app_id,
							text: 'go_to',
							type: 'url'
						}
					});
					this.props.t.socket.call('games.share', {gameId});
				} catch (e) {
				}
				this.props.t.setPopout(null);
			});
		} catch (e) {
			console.error(e);
			this.props.t.setPopout(null);
			this.props.t.setState({
                snackbar: <Snackbar onClose={() => this.props.t.setState({snackbar: null})}>
							 
                        </Snackbar>
            });
		}
    }

    render() {
        const
            {t} = this.props,
            isDesktop = isPlatformDesktop(),
            {user, gamesAsWinner, top} = t.state,
            {guessedPictures} = user
        ;

        return (
            <React.Fragment>
                <PanelHeader
                    left={<PanelHeaderBack onClick={t.back}/>}
                    separator={false}
                />
                <div className='Rating'>
                    {
                        top &&
                        <React.Fragment>
                            <p>
                                   :
                            </p>
                            <div className='Users_List'>
                                {
                                    top.map((value, index) =>
                                        <div key={`div__${index}`}>
                                            <Avatar size={48} shadow={false} src={value.photo_100}/>
                                            <div>
                                                <div>{value.first_name} {value.last_name.substring(0, 1)}.</div>
                                                <div>{value.sex === 1 && ''} {decOfNum(value.guessedPicturesToday, ['', '', ''])}</div>
                                            </div>
                                        </div>
                                    )
                                }
                            </div>
                        </React.Fragment>
                    }
                    {
                        gamesAsWinner.length > 0 &&
                        <React.Fragment>
                            <p style={{margin: '24px 0 0 0'}}>
                                  {decOfNum(guessedPictures, ['', '', ''])} (  ,     ):
                            </p>
                            <div style={{marginTop: 18, gridGap: isDesktop ? 8 : 15}} className='Images_Grid'>
                                {
                                    gamesAsWinner.map((value, index) =>
                                        <div key={`Image_${index}`} onClick={async () => {
                                            //this.shareStory(value.id, value['Picture.url']);
                                            await t.setState({
                                                like_currentPictureSelected: index,
                                                like_method: 'games.getByWinnerId',
                                                like_param: 'gamesAsWinner'
                                            });
                                            t.go('picture_info');
                                        }}>
                                            <img alt='img' src={value['Picture.url']}/>
                                        </div>
                                    )
                                }
                            </div>
                        </React.Fragment>
                    }
                    <div ref={ref => this.loadingRef = ref}/>
                </div>
            </React.Fragment>
        )
    }
}

Rating.defaultProps = {};

Rating.propTypes = {
    t: PropTypes.object
};

export default Rating;